* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@media only screen and (max-width: 768px) {
  html {
    font-size: 12px;
  }
}

body {
  --primary: #ebc7b5;
  --secondary: #0f86a1;
  --white: #fff;
  --black: #333;
  --gray: #1b1b1bbb;
  --light-bg: #f8efe5;
  --subheading: #fff9;
  --input-br: #ffffff4d;
  --link: #666;
  --animation-delay: .5s;
}

button {
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  padding: 0;
}

section {
  width: 100%;
  max-width: 1310px;
  margin: 0 auto;
}

.glide-up {
  animation: 1.5s cubic-bezier(.25, .46, .45, .94) both glide-up;
  animation-delay: var(--animation-delay);
}

.glide-down {
  animation: 1.5s cubic-bezier(.25, .46, .45, .94) both glide-down;
  animation-delay: var(--animation-delay);
}

.glide-down-close {
  animation: 1s cubic-bezier(.25, .46, .45, .94) both glide-down-close;
  animation-delay: var(--animation-delay);
}

.glide-up-close {
  animation: 1s cubic-bezier(.25, .46, .45, .94) both glide-up-close;
  animation-delay: var(--animation-delay);
}

.fade-in {
  animation: 1.5s cubic-bezier(.25, .46, .45, .94) both fade-in;
  animation-delay: var(--animation-delay);
}

.scale-up {
  animation: .5s cubic-bezier(.25, .46, .45, .94) both scale-up;
  animation-delay: var(--animation-delay);
}

@keyframes glide-down {
  from {
    opacity: 0;
    transform: translateY(-80px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes glide-up {
  from {
    opacity: 0;
    transform: translateY(80px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes glide-down-close {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes glide-up-close {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes scale-up {
  from {
    transform: scale(.9);
  }

  to {
    transform: scale(1);
  }
}

