.page--out-of-view .glide-up, .page--out-of-view .glide-down, .page--out-of-view .glide-down-close, .page--out-of-view .glide-up-close, .page--out-of-view .scale-up, .page--out-of-view .fade-in {
  opacity: 0;
  animation: none;
}

